.forum {
  all: unset;
  display: grid;
  grid-template-columns: minmax(0, 1fr);
  gap: 1em;
}

.topic-item {
  position: relative;
  display: grid;
  grid-template-rows: repeat(2, auto);
  padding-inline: 1em;
  background-color: var(--colorG27);
  border-radius: var(--r1);
  box-shadow: var(--shadow1); /* var(--shadow3); */
  gap: 0 1em;

  & .topic-link {
    position: absolute;
    inset: 0;
    z-index: 1;

    &:where(:hover, :active) + * h2 a {
      color: var(--colorW);
      text-decoration: underline;
    }
  }

  & > * a {
    position: relative;
    z-index: 3;
    text-decoration: none;
  }

  & > :nth-child(2),
  & > :nth-child(3) {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    gap: 0 1em;
  }

  & :where(p, ul, li) {
    all: unset;
  }

  & > :nth-child(2) {
    grid-area: ttt;
  }

  & > :nth-child(2) div {
    flex-shrink: 0;
  }

  & > :nth-child(3) {
    flex-wrap: wrap;
  }

  & > :nth-child(3) p {
    justify-content: start;
    margin-block: 0.25em 1em;
  }

  & > :nth-child(3) ul {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5em 0.5em;
    font-size: 0.8em;
    margin-block-end: 1em;
  }

  & > :nth-child(3) ul > :last-child {
    margin-inline-end: 0;
  }

  & > :nth-child(3) ul li {
    margin-block-end: 0.5em;
  }

  & > :nth-child(3) ul a {
    padding: 0.25em 0.5em 0.25em 1.25em;
    white-space: nowrap;
    background-color: var(--colorG15);
    clip-path: polygon(0.75em 0, 100% 0, 100% 100%, 0.75em 100%, 0 50%);
  }

  & h2 {
    all: unset;
    margin-block: 1.25em 0;
    font-weight: bold;
    text-wrap: balance;

    & a:where(:focus, :hover, :active) {
      text-decoration: underline;
    }
  }
}

@media (width < $sizeXS) {
  .topic-item {
    grid-template-columns: minmax(0, 1fr) 2em;
    grid-template-areas: 'ttt ttt' 'uuu aaa';
  }
}

@media (width > $sizeXS) {
  .topic-item {
    grid-template-columns: 4em minmax(0, 1fr);
    grid-template-areas: 'aaa ttt' 'aaa uuu';
  }
}

.avatars {
  grid-area: aaa;
  position: relative;
  margin: auto;

  &,
  & > * {
    width: 3.5em;
    height: 3.5em;
    background-image: var(--img, '');
    background-color: var(--colorG13);
    background-size: cover;
    border-radius: 50%;
    border: 0.2em solid var(--colorG27);
  }

  & > * {
    position: absolute;
  }

  & > :nth-child(1) {
    transform: translate(1.2em, 1em) scale(0.5);
  }

  & > :nth-child(2) {
    transform: translate(1.2em, 0.2em) scale(0.5);
  }

  & > :nth-child(3) {
    transform: translate(1.2em, -0.6em) scale(0.5);
  }
}

@media (width < $sizeXS) {
  .avatars {
    margin-block-end: 2em;
    font-size: 0.5em;
  }
}

.topic-solved > :nth-child(2) svg:first-child {
  color: var(--colorS4);
}
